import { request } from 'utils';

// request.prefix = '/api/categories';
const prefix = '/api/students';

export const getEnrollSolutions = ({ page, pageSize = 10, orderBy, isPagingEnabled = true }) =>
  request({
    prefix,
    url: `/EnrollSolution`,
    method: 'GET',
    params: { page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const createEnrollSolutions = ({
  id,
  label,
  courseId,
  minTotalPoint,
  minPointPerSubject,
  noOfStudent,
}) =>
  request({
    prefix,
    url: `/EnrollSolution`,
    data: { id, label, courseId, minTotalPoint, minPointPerSubject, noOfStudent },
    method: 'POST',
  });

export const deleteEnrollSolutions = id =>
  request({
    prefix,
    url: `/EnrollSolution/${id}`,
    method: 'DELETE',
  });

export const getEnrollSolutionsById = id =>
  request({
    prefix,
    url: `/EnrollSolution/${id}`,
    method: 'GET',
  });

export const updateEnrollSolutions = ({
  id,
  label,
  courseId,
  minTotalPoint,
  minPointPerSubject,
  noOfStudent,
}) =>
  request({
    prefix,
    url: `/EnrollSolution/${id}`,
    data: { id, label, courseId, minTotalPoint, minPointPerSubject, noOfStudent },
    method: 'PUT',
  });



